.header {
  background-color: #242526;
  width: 100%;
  height: 56px;
  position: fixed;
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.logo-search {
  width: 28%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.logo-search img {
  width: 45px;
  height: 45px;
  object-fit: contain;
  margin-right: 5px;
}

.logo-search div {
  width: 80%;
  height: 70%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 45px;
  background-color: #3A3B3C;
}

.logo-search div .icon {
  color: #B0B3B8;
}

.logo-search div input {
  width: 80%;
  height: 90%;
  border-radius: 45px;
  border-style: none;
  background-color: #3A3B3C;
  color: #B0B3B8;
  font-family: "Segoe UI Historic", "Segoe UI", Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;
  outline: none;
}

.tabs {
  width: 44%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.tabs img {
  width: 55px;
  height: 55px;
  object-fit: contain;
}

.profile {
  width: 28%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.profile img {
  width: 55px;
  height: 55px;
  object-fit: contain;
}

.dropdown-menu {
  position: absolute;
  top: 60px;
  right: 10px;
  width: 200px;
  background-color: #242526;
  padding: 10px;
  display: none;
  border-radius: 15px;
}

.dropdown-menu li {
  font-family: "Segoe UI Historic", "Segoe UI", Helvetica, Arial, sans-serif;
  color: #B0B3B8;
}

.dropdown-menu.open {
  display: block;
}
